html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Open Sans", sans-serif, Arial;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: rgb(25, 25, 25);
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.container {
  width: 100%;
  margin: 0;
  display: flex;
  height: 100vh;
}

img {
  max-width: 100%;
  display: block;
}

.sidebar__contenedor.close {
  width: 60px;
  align-items: center;
  justify-content: center;
}
.sidebar__contenedor.close .sidebar__contenedor-header-marca,
.sidebar__contenedor.close .sidebar__contenedor-usuario-datos,
.sidebar__contenedor.close .sidebar__contenedor-opciones-item-titulo {
  display: none;
}
.sidebar__contenedor.close .sidebar__contenedor-usuario {
  padding: 15px 0px;
  justify-content: center;
}
.sidebar__contenedor.close .sidebar__contenedor-opciones-item {
  align-items: center;
  justify-content: center;
}

.sidebar__contenedor {
  font-family: "Open Sans", sans-serif, Arial;
  width: 300px;
  height: 100%;
  background: rgb(25, 25, 25);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  transition: 0.3s all ease;
}
@media (max-width: 1000px) {
  .sidebar__contenedor {
    max-width: 75%;
  }
}
.sidebar__contenedor-header {
  width: 100%;
  display: flex;
  padding-left: 12px;
  background-color: rgb(35, 35, 35);
  gap: 20px;
  height: 80px;
  align-items: center;
}
.sidebar__contenedor-header-logo {
  color: white;
  padding-left: 10px;
  cursor: pointer;
}
.sidebar__contenedor-header-marca {
  color: white;
  font-size: 30px;
  font-weight: bold;
}

.sidebar__contenedor-usuario {
  width: 100%;
  background-color: rgb(35, 35, 35);
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
  padding: 15px 15px;
}
.sidebar__contenedor-usuario-logo {
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background-color: white;
}
.sidebar__contenedor-usuario-logo img {
  height: 90%;
  border-radius: 50%;
}
.sidebar__contenedor-usuario-datos {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}
.sidebar__contenedor-usuario-datos-titulo {
  font-weight: 500;
  color: white;
}
.sidebar__contenedor-usuario-datos-subtitulo {
  font-weight: 400;
  color: rgb(210, 210, 210);
  font-size: 0.8em;
}

.sidebar__contenedor-opciones {
  width: 100%;
  height: 100%;
  color: white;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.sidebar__contenedor-opciones-item {
  cursor: pointer;
  padding: 15px 25px;
  display: flex;
  gap: 15px;
  color: white;
  transition: all 0.3s ease;
  justify-content: flex-start;
}
.sidebar__contenedor-opciones-item-titulo {
  font-weight: 500;
}
.sidebar__contenedor-opciones-item-titulo a {
  color: white;
}
.sidebar__contenedor-opciones-item-titulo.logout {
  color: rgb(245, 77, 77);
}
.sidebar__contenedor-opciones-item-logo.logout {
  color: rgb(245, 77, 77);
}
.sidebar__contenedor-opciones-item:hover {
  color: rgb(210, 210, 210);
  background-color: rgb(40, 40, 40);
}
.sidebar__contenedor-opciones-item:hover a {
  color: rgb(210, 210, 210);
}

.btn-logout {
  margin-top: auto;
  margin-bottom: 20px;
  display: block;
}

:root {
  --verde-oscuro: #1D6F42;
  --rojo: rgb(255, 56, 86);
  --gris-claro: #e8e8e8;
  --gris-fuerte: #dedede;
  --btn-verde: #329469;
  --btn-azul: #26b5e0;
  --btn-rojo: #cc2a0d;
  --gris-thead-oscuro: #f3f3f3;
  --gris-tr-claro: #fafafa;
  --gris-input: #E6E6E6;
}

/*   Alumnos Inscritos   */
.container-main {
  width: 1200px;
  margin: 0 auto;
  margin-top: 40px;
}

.name-coordinador {
  font-size: 25px;
  margin-bottom: 10px;
}

.row-main {
  margin-left: 0px;
  width: 100%;
}

.alumnos-departamento {
  font-size: large;
  margin: 10px 0px;
}

.subcontainer1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

.contain-excel {
  display: flex;
  gap: 20px;
  align-items: center;
}

.subcontainer2 {
  display: flex;
  height: 50px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.contain-pdf {
  display: flex;
  gap: 20px;
  align-items: center;
}

.text-pdf-excel {
  width: 220px;
}

.btn-excel {
  width: 250px;
  cursor: pointer;
  padding: 8px 30px;
  border-radius: 10px;
  border: 0;
  background-color: var(--verde-oscuro);
  letter-spacing: 1.5px;
  font-size: 15px;
  transition: all 0.3s ease;
  box-shadow: rgb(8, 87, 35) 0px 10px 0px 0px;
  color: hsl(0deg, 0%, 100%);
}

.btn-excel:hover {
  box-shadow: rgb(8, 87, 35) 0px 7px 0px 0px;
}

.btn-excel:active {
  background-color: var(--verde-oscuro);
  box-shadow: rgb(8, 87, 35) 0px 0px 0px 0px;
  transform: translateY(5px);
  transition: 200ms;
}

.btn-danger:hover {
  box-shadow: #a1220c 0px 7px 0px 0px;
}

.btn-info:hover {
  box-shadow: #0e7b9c 0px 7px 0px 0px;
}

.btn-sucess:hover {
  box-shadow: #127449 0px 7px 0px 0px;
}

.btn-pdf {
  width: 250px;
  cursor: pointer;
  padding: 8px 30px;
  border-radius: 10px;
  border: 0;
  background-color: var(--rojo);
  letter-spacing: 1.5px;
  font-size: 15px;
  transition: all 0.3s ease;
  box-shadow: rgb(201, 46, 70) 0px 10px 0px 0px;
  color: hsl(0deg, 0%, 100%);
}

.btn-pdf:hover {
  box-shadow: rgb(201, 46, 70) 0px 7px 0px 0px;
}

.btn-pdf:active {
  background-color: var(--rojo);
  box-shadow: rgb(201, 46, 70) 0px 0px 0px 0px;
  transform: translateY(5px);
  transition: 200ms;
}

.input-style {
  width: 350px;
  border: none;
  padding: 1rem;
  border-radius: 1rem;
  background: var(--gris-claro);
  transition: 0.3s;
}

.input-style:focus {
  border: none;
  padding: 1rem;
  border-radius: 1rem;
  transition: 0.3s;
}

/*   Tabla   */
.table-info {
  width: 100%;
  margin-top: 20px;
}

.table-info thead {
  background-color: var(--gris-thead-oscuro);
  color: black;
  font-size: medium;
}

.table-info td, th {
  padding: 12px 15px;
}

.table-info tbody tr:nth-of-type(even) {
  background-color: var(--gris-thead-oscuro);
}

.table-info tbody tr:nth-last-of-type(even) {
  background-color: var(--gris-tr-claro);
}

.num-cuenta, .fecha-inicio, .fecha-fin {
  width: 10%;
}

.nombre-alumno {
  width: 25%;
}

.carrera {
  width: 25%;
}

.acciones {
  width: 20%;
}

.tbody-info {
  width: 100%;
  align-items: center;
  text-align: start;
  height: 50px;
}

tr {
  height: 50px;
}

.data-num-cuenta, .data-fecha-inicio, .data-fecha-fin {
  text-align: center;
}

.button-acciones {
  display: flex;
  justify-content: space-around;
  gap: 5px;
}

.btn-acc {
  text-align: center;
  border: none;
  outline: none;
  cursor: pointer;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  color: #fff;
  border-radius: 5px;
  transition: all ease 0.1s;
  width: 75px;
  height: 30px;
}

.btn-acc:active {
  transform: translateY(5px);
  box-shadow: 0px 0px 0px 0px #a29bfe;
}

.btn-sucess {
  background-color: var(--btn-verde);
  box-shadow: 0px 5px 0px 0px #127449;
}

.btn-sucess:active {
  box-shadow: 0px 0px 0px 0px #127449;
}

.btn-info {
  background-color: var(--btn-azul);
  box-shadow: 0px 5px 0px 0px #0e7b9c;
}

.btn-info:active {
  box-shadow: 0px 0px 0px 0px #0e7b9c;
}

.btn-danger {
  background-color: var(--btn-rojo);
  box-shadow: 0px 5px 0px 0px #9c200a;
}

.btn-danger:active {
  box-shadow: 0px 0px 0px 0px #9c200a;
}

/*   Alumnos Rechazados   */
.btn-alta, .btn-datos-rechazados {
  height: 40px;
  width: 90px;
  padding: 5px;
}

/*   Estadisticas   */
.container-estadisticas {
  width: 1200px;
  margin: 0 auto;
  margin-top: 40px;
}

.title-estadisticas {
  margin-top: 20px;
  margin-bottom: 10px;
