  font-size: 30px;
}

.tipo-dato {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 20px;
}

.container-checkbox {
  margin-top: 40px;
  display: flex;
  justify-content: space-around;
  margin-bottom: 40px;
}

.form-check-box {
  display: flex;
  align-items: center;
  gap: 10px;
}

.input-select {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: solid 1px #c4c4c4;
  background: #f3f3f3;
}

.input-select:hover {
  background-color: var(--gris-claro);
}

.space2 {
  height: 35px;
}

.space3 {
  height: 20px;
}

.space4 {
  height: 5px;
}

.exportar-pdf-excel {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.button-pdf-estadisticas button {
  display: flex;
  align-items: center;
}

.button-pdf-estadisticas img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.input-select-estadisticas {
  margin-top: 20px;
  border: solid 1px #c4c4c4;
}

.estadisticas-exportar-pdf-excel {
  margin-bottom: 30px;
}

.generar-documento {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.select-dpto-flex {
  display: flex;
  flex-direction: column;
}

.select-periodo-flex {
  display: flex;
  flex-direction: column;
}

.input-select-dpto-periodo {
  margin-top: 20px;
  padding: 10px;
  border-radius: 10px;
  background: #f3f3f3;
  border: solid 1px #c4c4c4;
}

.input-select-periodo {
  width: 250px;
}

.btn-documento {
  cursor: pointer;
  padding: 8px 30px;
  border-radius: 10px;
  border: 0;
  background-color: var(--btn-azul);
  letter-spacing: 1.5px;
  font-size: 15px;
  transition: all 0.3s ease;
  box-shadow: rgb(46, 157, 201) 0px 10px 0px 0px;
  color: hsl(0deg, 0%, 100%);
}

.btn-documento:hover {
  box-shadow: rgb(46, 157, 201) 0px 7px 0px 0px;
}

.btn-documento:active {
  background-color: var(--btn-azul);
  box-shadow: rgb(46, 157, 201) 0px 0px 0px 0px;
  transform: translateY(5px);
  transition: 200ms;
}

.input-select-dpto-periodo {
  width: 100%;
}

.span-danger {
  color: var(--btn-rojo);
  margin-top: 20px;
}

.select-download-document {
  margin-top: 40px;
}

@media (max-width: 1024px) {
  .generar-documento {
    flex-direction: column;
  }
  .select-download-document {
    width: 100%;
    margin-bottom: 60px;
    margin-top: 10px;
  }
}
@media (max-width: 768px) {
  .container-checkbox {
    flex-direction: column;
    align-items: center;
    gap: 35px;
  }
  .select-dpto-flex, .select-periodo-flex {
    align-items: center;
  }
  .select-dpto-flex {
    margin-top: 20px;
  }
  .title-estadisticas {
    text-align: center;
  }
  .input-select-dpto-periodo {
    width: 100%;
  }
}
@media (max-width: 600px) {
  .container-estadisticas {
    margin-left: 110px;
  }
  .exportar-pdf-excel {
    flex-direction: column;
    align-items: center;
  }
}
/*   Checkbox   */
@supports (-webkit-appearance: none) or (-moz-appearance: none) {
  .checkbox-wrapper-13 input[type=checkbox] {
    --active: #275EFE;
    --active-inner: #fff;
    --focus: 2px rgba(39, 94, 254, .3);
    --border: #BBC1E1;
    --border-hover: #275EFE;
    --background: #fff;
    --disabled: #F6F8FF;
    --disabled-inner: #E1E6F9;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 21px;
    outline: none;
    display: inline-block;
    vertical-align: top;
    margin: 0;
    cursor: pointer;
    border: 1px solid var(--bc, var(--border));
    background: var(--b, var(--background));
    transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
  }
  .checkbox-wrapper-13 input[type=checkbox]:after {
    content: "";
    display: block;
    left: 0;
    top: 0;
    position: absolute;
    transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
  }
  .checkbox-wrapper-13 input[type=checkbox]:checked {
    --b: var(--active);
    --bc: var(--active);
    --d-o: .3s;
    --d-t: .6s;
    --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
  }
  .checkbox-wrapper-13 input[type=checkbox]:disabled {
    --b: var(--disabled);
    cursor: not-allowed;
    opacity: 0.9;
  }
  .checkbox-wrapper-13 input[type=checkbox]:disabled:checked {
    --b: var(--disabled-inner);
    --bc: var(--border);
  }
  .checkbox-wrapper-13 input[type=checkbox]:disabled + label {
    cursor: not-allowed;
  }
  .checkbox-wrapper-13 input[type=checkbox]:hover:not(:checked):not(:disabled) {
    --bc: var(--border-hover);
  }
  .checkbox-wrapper-13 input[type=checkbox]:focus {
    box-shadow: 0 0 0 var(--focus);
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch) {
    width: 21px;
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch):after {
    display: none;
    position: relative;
    opacity: var(--o, 0);
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch):checked {
    --o: 1;
  }
  .checkbox-wrapper-13 input[type=checkbox] + label {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin-left: 4px;
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch) {
    border-radius: 7px;
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch):after {
    width: 5px;
    height: 9px;
    border: 2px solid var(--active-inner);
    border-top: 0;
    border-left: 0;
    left: 7px;
    top: 4px;
    transform: rotate(var(--r, 20deg));
  }
  .checkbox-wrapper-13 input[type=checkbox]:not(.switch):checked {
    --r: 43deg;
  }
}
.checkbox-wrapper-13 * {
  box-sizing: inherit;
}

.checkbox-wrapper-13 *:before,
.checkbox-wrapper-13 *:after {
  box-sizing: inherit;
}

